import axios from 'axios';
import { createStore } from 'vuex';

const store = createStore({
  state (){
    return{
      name : 'kim',
      age : 20,
      likes : 10,
      likeClick : true,
      more : {},
    }
  },
  mutations :{
    nameChange(state){
      state.name = 'kang';
    },
    agePlus(state){
      state.age++;
    },
    likePlus(state){
      if(state.likeClick){
        state.likeClick = false;
        state.likes++;
      } else {
        state.likeClick = true;
        state.likes--
      }
    },
    setMore(state, data){
      state.more = data
    },
  },
  actions : {
    getData(context){
      axios.get(`https://codingapple1.github.io/vue/more0.json`).then((a)=>{
        console.log(a.data);
        context.commit('setMore', a.data)
      })
    },
  },
})

export default store;